import Time from '@/common/time.js'
import util from '@/common/util.js'
export default {
	props:{
		userItem:{
			type:Object
		}
	},
	methods:{
		toInfo(id){
			uni.navigateTo({
				url:'../Info/Info?id='+id
			})
		}
	},
	computed:{
		time(){
			let stamp=Time.getMyUninx(this.userItem.time)
			return Time.getFormatTime(stamp);
		}
	},
}
